.app {
  text-align: center;
  color: white;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media all and (min-width: 640px) {
    padding: 50px;
 }
}
* {
  margin: 0px;
  list-style: none;
  box-sizing: border-box;
}
ul {
  padding-inline-start: 0px;
}
a {
  color: white;
  text-decoration: none;
}
body {
  width: 100%;
  height: 100%;
  background-color: black;
}
img {
  max-width: 100%;
  max-height: 100%;
}
